  .cellWrapper{
    display: flex;
    align-items: center;
  
    .image {
      width: 32px;
      height: 32px;
      border-radius: 50%;
      margin-right: 10px;
      object-fit: cover;
    } 
  }
  
  .status{
    padding: 5px;
    border-radius: 5px;

    &.Approved {
      color: green;
      background-color:rgba(0,128,0,0.151) ;
    }
    &.Pending {
      color: goldenrod;
      background-color:rgba(118, 118, 121, 0.243) ;
    }
  }
