.navbar{
  height: 50px;
  border-bottom: 0.5px solid rgb(235, 231, 231) ;
  display: flex;
  flex:6;
  align-items: center;
  font-size: 14px;
  color: rgb(37, 36, 36);
  
  .wrapper{
    width: 100%;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;

  .search{
    display: flex;
    align-items: center;
    border: 0.5px solid lightgray;
    padding: 3px;
    border-radius: 20px;
    
  }
input{
  border:none;
  outline: none;
  background-color: transparent;

  &::placeholder{
    font-size:14px;
    
  } 
}
  .items{
    display: flex;
    align-items: center;

    .item{
      display: flex;
      align-items: center;
      margin-right: 20px;
      position: relative;
      
      .icon{
        font-size: 20px;
        }
        .avatar{
          width: 30px;
          height: 30px;
          border-radius: 50%;
        }

      .counter{
        width: 15px;
        height: 15px;
        background-color: rgb(211, 10, 10);
        border-radius: 50%;
        color: white;
        align-items: center;justify-content: center;
        display: flex;
        font-size: 10px;
        font-weight: bold;
        position: absolute;
        top:-5px;
        right: -5px;
       }
     }

    } 

  } 

}